import React from 'react';
import useTranslation from "../../../hooks/useTranslation";

const WooCommerceFieldsMiniTable = ({id, elements}) => {

    return (
        <div className="acpt-table-responsive">
            <table className={`acpt-minitable ${document.globals.is_rtl === true ? `rtl` : ``}`}>
                <tr>
                    <th>
                        {useTranslation("Name")}
                    </th>
                    <th>
                        {useTranslation("Type")}
                    </th>
                </tr>
                {elements.map((element)=>(
                    <tr>
                        <td>{element.name}</td>
                        <td>{element.type}</td>

                    </tr>
                ))}
            </table>
            <div className="minitable-buttons">
                <a href={`#/product-data/product/fields/${id}`} >
                    {useTranslation("Manage")}
                </a>
            </div>
        </div>
    );
};

export default WooCommerceFieldsMiniTable;